(function() {
  'use strict';

  angular
    .module('leapgesture', ['ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAnimate', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr', 'ui-rangeSlider']);

})();

/* global Leap:false */
(function() {
  'use strict';

  angular
    .module('leapgesture')
    .provider('leapController', leapControllerProvider);


  function leapControllerProvider() {

    var controller = new Leap.Controller({background: true})
        .use('playback', {
          // recording: '/api/gestures/20/data.lz', // this is a test
          loop: false,
          pauseHotkey: false,
          pauseOnHand: false,
          autoPlay : false
        })
        .use('riggedHand')
        .connect();


    var player =  controller.plugins['playback'].player;

    this.$get = function leapController() {
      return {
        controller : controller,
        player : player
      };
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('leapgesture')
    .controller('SaveRecordingModalController', SaveRecordingModalController);

  /** @ngInject */
  function SaveRecordingModalController($scope, $uibModalInstance) {

    $scope.gestureName = "My Gesture";

    $scope.save = function() {
      $scope.$close($scope.gestureName);
    }

    $scope.cancel = function() {
      $scope.$dismiss("Dismissed");
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('leapgesture')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, leapController, $resource, $uibModal, $log) {

    var vm = this;

    vm.view = {
      loading : false,
      currentGestureId : null,
      recordedGesture : {
        name : "My Gesture"
      },
      playing : false,
      unsavedRecording : false
    };

    var Gesture = $resource("/api/gestures/:id", { id: "@id" },
      {
        'create':  { method: 'POST' },
        'index':   { method: 'GET', isArray: false },
        'show':    { method: 'GET', isArray: false },
        'update':  { method: 'PUT' },
        'destroy': { method: 'DELETE' }
      }
    );

    vm.gestures = [];

    vm.player = leapController.player;

    leapController.controller.on('playback.recordingFinished', function(){
      //vm.view.unsavedRecording = true;
      //$scope.$apply();
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/main/saveRecordingModal.html',
        controller: 'SaveRecordingModalController',
        controllerAs: 'modal',
        size: 'sm'
      });

      modalInstance.result.then(function (name) {
        vm.view.recordedGesture.name = name;
        vm.save();
      });
    });

    leapController.controller.on('playback.playbackFinished', function(){
      vm.view.playing = false;
      $scope.$apply();
    });

    vm.toggle = function() {
      vm.view.playing = !vm.view.playing;
      vm.player.toggle();
    };

    vm.play = function() {
      vm.view.playing = true;
      leapController.player.play();
    };

    vm.pause = function() {
      vm.view.playing = false;
      leapController.player.pause();
    };

    vm.clear = function() {
      leapController.player.stop();
      vm.view.playing = false;
    };

    vm.selectGesture = function(id) {
      if (id === vm.view.currentGestureId) {
        vm.view.currentGestureId = null;
        if (vm.player.playing) {
          vm.player.stop();
        }
        vm.view.playing = false;
        vm.clear();
        return;
      }
      vm.view.unsavedRecording = false;
      vm.view.loading = true;
      // get the recording from the server
      Gesture.show({id : id}, function(response){
        leapController.player.setRecording({'compressedRecording' : response.data.data});
        vm.view.currentGestureId = response.data.id;
        vm.view.loading = false;
        vm.play();
      });
    };

    vm.record = function() {
      leapController.player.record();
    };

    vm.delete = function() {
      vm.view.loading = true;
      Gesture.destroy({id : vm.view.currentGestureId}, function(){
        vm.view.loading = false;
        loadGestures();
      });
    };

    vm.save = function() {
      var gesture = leapController.player.recording.export('lz');
      var newGesture = new Gesture({'data': gesture, 'name' : vm.view.recordedGesture.name});
      newGesture.$save().then(function(){
        vm.view.unsavedRecording = false;
        loadGestures();
      });
    };

    // We can retrieve a collection from the server
    var loadGestures = function() {
      vm.view.loading = true;
      Gesture.index(function(response) {
        vm.gestures = response.data;
        vm.view.loading = false;
      });
    };

    loadGestures();

  }

})();

(function() {
  'use strict';

  angular
    .module('leapgesture')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('leapgesture')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('leapgesture');

})();

(function() {
  'use strict';

  angular
    .module('leapgesture')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

  }

})();

angular.module("leapgesture").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container-fluid\">\n  <div class=\"loading\" ng-show=\"main.view.loading\"><span class=\"loading-spin\"></span></div>\n  <div class=\"row\">\n    <div class=\"controls col-md-3\">\n      <h1>LeapGesture</h1>\n      <p>This app requires a Leap Motion controller connected to your computer. Record hand gestures, save them, and play them back.</p>\n      <ul class=\"gestures\">\n          <li ng-repeat=\"gesture in main.gestures\" ng-class=\"{ selected : gesture.id === main.view.currentGestureId }\"><span ng-click=\"main.selectGesture(gesture.id)\">{{gesture.name}}</span></li>\n      </ul>\n      <div class=\"row\">\n        <div class=\"col-md-12\">\n          <button class=\"btn btn-default\" ng-click=\"main.record()\"><span class=\"glyphicon glyphicon-record\"></span> Record</button>\n          <button class=\"btn btn-default play-pause\" ng-disabled=\"!main.player.recording || !main.view.currentGestureId\" ng-click=\"main.toggle()\">\n            <span ng-show=\"!main.view.playing\"><span class=\"glyphicon glyphicon-play\"></span> Play</span>\n            <span ng-show=\"main.view.playing\"><span class=\"glyphicon glyphicon-pause\"></span> Pause</span>\n          </button>\n         <!-- <button class=\"btn btn-default play-pause\" ng-show=\"main.view.playing\" ng-disabled=\"!main.player.recording || !main.view.currentGestureId\" ng-click=\"main.pause()\"><span class=\"glyphicon glyphicon-pause\"></span> Pause</button> -->\n          <button class=\"btn btn-default\" ng-disabled=\"!main.player.recording || !main.view.currentGestureId\" ng-click=\"main.delete()\"><span class=\"glyphicon glyphicon-trash\"></span> Delete</button>\n        </div>\n      </div>\n    </div>\n  </div>\n</div>\n");
$templateCache.put("app/main/saveRecordingModal.html","<div class=\"modal-header\">\n    <h3 class=\"modal-title\">Save Gesture</h3>\n</div>\n<div class=\"modal-body\">\n  <form>\n    <div class=\"form-group\">\n      <input type=\"text\" class=\"form-control\" placeholder=\"Gesture name\" ng-model=\"gestureName\">\n    </div>\n  </form>\n</div>\n<div class=\"modal-footer\">\n    <button class=\"btn btn-primary\" type=\"button\" ng-click=\"save()\">Save</button>\n    <button class=\"btn btn-default\" type=\"button\" ng-click=\"cancel()\">Discard</button>\n</div>\n");}]);